.underline-link {
	border-bottom: 1px solid transparent;
	transition: border 300ms;
	@include hover {
		text-decoration: none;
		border-bottom: 1px solid black; }

	&_gray {
		@include hover {
			border-color: $text-dark-gray; } }

	&_behance {
		@include hover {
			border-color: #053EFF; } }
	&_dribble {
		@include hover {
			border-color: #EA4C89; } } }

